	module.exports = {"data":{"markdownRemark":{"html":"<p>添加一个404页面很容易。 只需创建一个路径匹配正则表达式/ 404 *的页面。 大多数情况下，您只需要在src/pages/404.js创建一个React组件页面。</p>\n<p>Gats确保你的构建的404页面是404.html，并且它作为许多静态主机平台默认使用的404错误页面。 如果您以另一种方式托管您的网站，则需要设置一个自定义规则来为此文件提供404错误。</p>\n<p>开发时，Gatsby添加一个覆盖自定义404页面的默认404页面。 但是，您仍然可以访问您的404页面的确切网址，以验证其正常工作。</p>","excerpt":"添加一个404页面很容易。 只需创建一个路径匹配正则表达式/ 404 *的页面。 大多数情况下，您只需要在src/pages/404.js创建一个React组件页面。 Gats确保你的构建的404页面是404.html，并且它作为许多静态主机平台默认使用的40…","timeToRead":1,"frontmatter":{"title":"添加404页面"},"parent":{"__typename":"File","relativePath":"docs/add-404-page.md"}}},"pathContext":{"slug":"/docs/add-404-page/"}}

